import React, {useContext, useState} from 'react';
import { Navbar,Container,Nav, Button} from 'react-bootstrap';
import './App.css';
import Data from './data.js';
import Detail from './Detail.js'
import axios from 'axios';
import Cart from './Cart.js'

import { Link, Route, Switch } from 'react-router-dom';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';


function App() {
  let [shoes, shoes변경] = useState(Data);
  let [재고, 재고변경] = useState([10,11,12]);


  return (
    <div className="App">
      <Navbar bg="light" variant="light">
        <Container>
        <Navbar.Brand href="/">shop</Navbar.Brand>
        <Nav className="me-auto">
          <Nav.Link as={Link} to="/">Home</Nav.Link>
          <Nav.Link as={Link} to="/Detail">Detail</Nav.Link>
          <Nav.Link>Pricing</Nav.Link>
        </Nav>
        </Container>
      </Navbar>
      <Switch>
        <Route exact path="/"> 
          <div className="jumbotron">
            <h1>20% season off </h1>
            <p>
              때 이른 봄 몇 송이 꺾어다 너의 방문앞에 두었어 <br/>
              긴잠 실컷자고 나오면 그 때쯤엔 예쁘게 피어 있겠다
            </p>
            <p>
              <Button variant="light">겨울잠</Button>
            </p>
          </div>
          <div className="container">

            <div className="row">
              {
                shoes.map((a,i)=>{
                  return <Product shoes={shoes[i]} key={i} i={i}></Product>
                })
              }
            </div>
            <button className="btn btn-primary" onClick={()=>{
              axios.get('https://codingapple1.github.io/shop/data2.json')
              .then((result)=>{ 
                shoes변경([...shoes, ...result.data ] )
                shoes.map((a,i)=>{
                  return <Product shoes={shoes[i]} key={i} i={i}></Product>
                })
              })
              .catch(()=>{ 
                console.log('tlfvo')
              })
            }}>더보기</button>
          </div>
        </Route>
        <Route exact path='/detail'>
          <div className="container">
            <div className="row">
              {
                shoes.map((a,i)=>{
                  return <Product shoes={shoes[i]} key={i} i={i}></Product>
                })
              }
            </div>
            <button className="btn btn-primary" onClick={()=>{
              axios.get('https://codingapple1.github.io/shop/data2.json')
              .then((result)=>{ 
                shoes변경([...shoes, ...result.data ] )
                shoes.map((a,i)=>{
                  return <Product shoes={shoes[i]} key={i} i={i}></Product>
                })
              })
              .catch(()=>{ 
                console.log('tlfvo')
              })
            }}>더보기</button>
          </div>
        </Route>
        <Route exact path="/detail/:id">
          <Detail shoes={shoes} 재고={재고} 재고변경={재고변경} />
        </Route>

        <Route path="/cart">
          <Cart></Cart>
        </Route>
        {/* <Rouet path="/어쩌구" component={Modal}></Rouet> */}
      </Switch>
    </div>
  );
}

function Product(props){

  let history = useHistory();

  return(
    <div className="col-md-4" onClick={()=>{ history.push('/detail/' + props.shoes.id) }}>
      <img src={ 'https://codingapple1.github.io/shop/shoes' + (props.i+1) + '.jpg'} width="100%" />
      <h4>{ props.shoes.title }</h4>
      <p>{ props.shoes.content} &amp; { props.shoes.price}</p>
    </div>
  )
}

export default App;
