import React, {useEffect, useState} from 'react';
import {useHistory, useParams} from 'react-router-dom';
import { Navbar,Container,Nav, Button} from 'react-bootstrap';
import styled from 'styled-components';
import './Detail.scss'

import {CSSTransition} from "react-transition-group";

import {connect} from 'react-redux';

function Detail(props){

  let [얼럿, 얼럿변경] =useState(true);
  let [inputData, inputData변경] = useState('');
  let [누른탭, 누른탭변경] = useState(0);
  let [스위치, 스위치변경] = useState(false)

  useEffect(()=>{
    let 타이머 = setTimeout(()=>{ 얼럿변경(false)},2000);
    return ()=>{clearTimeout(타이머)}
    },[얼럿]);

  let { id } = useParams();
  let 찾은상품 = props.shoes.find(function(상품){
      return 상품.id == id //이게 참일 경우에만 데이터를 새로운 변수에 저장해줌.
  })
  let history = useHistory();

  return(
    <div className="container">
      <div>
        <h4 className="red" >Detail</h4>
      </div>
      {
        얼럿 === true
        ? (
        <div>
          <p>재고 얼마 안남음</p>
        </div>
        )
        : null
      }
      <div className="row">
        <div className="col-md-6">
          <img src={'https://codingapple1.github.io/shop/shoes' + (찾은상품.id+1) + '.jpg'} width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{찾은상품.title}</h4>
          <p>{찾은상품.content}</p>
          <p>{찾은상품.price}</p>
          <Info 재고={props.재고}/>
          <button className="btn btn-danger" onClick={()=>{
            props.dispatch({
              type:'항목추가', payload : {id : 찾은상품.id, name: 찾은상품.title , quan:1}
            });
            history.push('/cart')
          }}>주문하기</button> 
          <button className="btn btn-danger" onClick={()=>{
              history.goBack();
          }}>뒤로가기</button> 
        </div>
      </div>
      <Nav variant="tabs" defaultActiveKey="link-0">
        <Nav.Item>
          <Nav.Link eventKey="link-0" onClick={()=>{ 스위치변경(false); 누른탭변경(0) }}>Active</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-1" onClick={()=>{ 스위치변경(false); 누른탭변경(1) }}>Option 2</Nav.Link>
        </Nav.Item>
      </Nav>
      <CSSTransition in={스위치} classNames="wow" timeout={500}>
        <TabContent 누른탭={누른탭} 스위치변경={스위치변경}/>
      </CSSTransition>
    </div> 
  )
}

function TabContent(props){

  useEffect(()=>{
    props.스위치변경(true)
  })

  if(props.누른탭 === 0){
    return <div>0번쨰 내용</div>
  } else if (props.누른탭 === 1){
    return <div>1번쨰 내용</div>
  } else if (props.누른탭 === 2){
    return <div>2번쨰 내용</div>
  } 
}


function Info(props){
  return(
    <p> 재고 : {props.재고[0] }</p>
  )
}

function state를props화(state){
  return {
    state : state.reducer,
    alert열렸니 : state.reducer2 //리듀서2에 있는거 가져오는법
  }
}

export default connect(state를props화)(Detail)